import React,{useContext,Fragment} from 'react'

import './SequenceControls.scss'
import {MainContext} from '../../contexts/MainContext'
import PoseToImg from '../PoseToImg/PoseToImg'

export let currentPose
export default function SequenceControls(){
	const {state,dispatch}=useContext(MainContext)
	const {sequencePoses,sequenceSeekPos,simPose,sequenceEditFrame}=state
	if(sequenceSeekPos===sequencePoses.length) currentPose=JSON.stringify(simPose)
	return (
		<div id="SequenceControls">
			<div className="top-row row">
				<div className="f-center px-8 py-4 spb-x-8">
					<div className="icon icon-layout_directed"/>
					<div className="b-700">Sequence Controls</div>
				</div>
				{sequenceEditFrame===undefined?
					<Fragment>
						<div className="row f-center spc-x-8 spc-4" style={{visibility: sequencePoses.length?'visible':'hidden'}}>
							<button
								className="outline super-center h7 icon icon-fast_backward"
								onClick={()=>dispatch(['SEQ_SEEK',0])}/>
							<button
								className="outline super-center h7 icon icon-step_backward"
								onClick={()=>dispatch(['SEQ_SEEK',Math.round(sequenceSeekPos)-1])}/>
							<span className="h7">Slider position</span>
							<input
								className="text-center h7 b-500"
								type="number"
								value={sequenceSeekPos+1}
								onChange={({target:{valueAsNumber}})=>dispatch(['SEQ_SEEK',valueAsNumber-1])}/>
							<button
								className="outline super-center h7 icon icon-step_forward"
								onClick={()=>dispatch(['SEQ_SEEK',Math.round(sequenceSeekPos)+1])}/>
							<button
								className="outline super-center h7 icon icon-fast_forward"
								onClick={()=>dispatch(['SEQ_SEEK',sequencePoses.length])}/>
						</div>
						<div className="col"/>
						<div className="px-8 py-4">
							<button
								className="outline super-center"
								style={{fontSize: '14px'}}
								onClick={()=>dispatch(['SET_STATE',{sequencePoses: [],sequenceSeekPos: 0}])}>
								<i className="fa fa-times mr-6"/>
								Clear
							</button>
						</div>
					</Fragment>:
					<Fragment>
						<button className="h7 my-4 mx-8 py-0 px-8 b-600 super-center" onClick={()=>dispatch(['SEQ_EDIT',undefined])}>
							<i className="fa fa-times mr-4"/>
							Exit Edit Mode
						</button>
						<div className="col"/>
					</Fragment>}
				{/* TODO: Unhide */}
				{/*<div className="row spc-x-8 spc-4">
					<button className="outline super-center" style={{fontSize: '14px'}}>
						<i className="fa fa-bookmark mr-6"/>
						Save Sequence
					</button>
				</div>*/}
				<div className="fa fa-minus f-center px-16 ml-auto" onClick={()=>dispatch(['SET_STATE',{showSequenceControl: false}])}/>
			</div>
			<div className="scroll-x">
				<div className="seekbar f-center" style={{height: '30px'}}>
					<div className="h7 text-center pr-8" style={{width: '72px'}}>Seekbar</div>
					{sequencePoses.length>=1?
						<Fragment>
							<input
								type="range"
								min="0"
								max={sequencePoses.length}
								step={1/100}
								value={sequenceSeekPos}
								onChange={({target:{valueAsNumber}})=>dispatch(['SEQ_SEEK',valueAsNumber])}
								style={{'--width': (sequencePoses.length)*144+'px'}}
								disabled={sequenceEditFrame!==undefined}/>
							<div className="h7 ml-16">{
								(sequenceEditFrame===undefined)?
									'Double click a keyframe to edit':
									''
							}</div>
						</Fragment>:undefined}
				</div>
				<div className="row spc-8 spc-x-16 px-0 f-nowrap">
					{state.sequencePoses.map((p,i)=><div
						className="poseimg-container"
						key={i}
						style={{opacity: sequenceEditFrame!==undefined && sequenceEditFrame!==i?.5:1}}>
						<div className="fa fa-minus" onClick={()=>dispatch(['SEQ_DEL',i])}/>
						<PoseToImg
							pose={p}
							onClick={()=>dispatch([sequenceEditFrame!==undefined?'SEQ_EDIT':'SEQ_SEEK',i])}
							onDoubleClick={()=>dispatch(['SEQ_EDIT',i])}
							cache={sequenceEditFrame!==i}/>
					</div>)}
					<div
						className={'needle button'+(sequenceEditFrame!==undefined?' edit-mode':'')}
						style={{
							opacity: +(sequencePoses.length && (sequenceSeekPos!==sequencePoses.length)),
							transform: `translateX(${72-(sequencePoses.length-sequenceSeekPos)*144}px)`
						}}
						onDoubleClick={()=>{ if(sequenceEditFrame!==undefined) dispatch(['SEQ_EDIT',undefined]) }}/>
					{sequenceEditFrame===undefined?
						<div className="fa fa-plus" onClick={()=>dispatch(['SEQ_ADD'])}/>: // TODO: Dashed border png
						undefined
					}
				</div>
			</div>
		</div>
	)
}
